import { Link } from 'react-router-dom';

const HomeTop = () => {
  return (
    <div>
    <header className="App-header">
    <img src={process.env.PUBLIC_URL + './img/logo.png'} />
      <Link to="/" style={{ textDecoration: "none"}}><h1 style={{ color: "black"}}></h1></Link>
      <nav>
        <ul>
          <li>
            <Link to="/about"></Link>
          </li>
          <li>
            <Link to="/study"></Link>
          </li>
          <li>
            <Link to="/bus"> </Link>
          </li>
          <li>
            <Link to="/articles"> </Link>
          </li>       
        </ul>
      </nav>
      
    </header> 
    </div>
  );
}

export default HomeTop;
